/* Navbar container styles */
.navbar {
  background-color: #3498db;
  padding: 1rem;
}

/* Site Name link styles */
.navbar a {
  color: #fff;
  text-decoration: none;
}

/* Navigation links styles */
.navbar ul {
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.navbar li {
  margin-right: 1rem;
}

.navbar li a {
  color: #fff;
  text-decoration: none;
}